import { getLeavesClient, isClientError } from '@mintlify/http-client';
import { GetStaticPaths, GetStaticProps } from 'next';

import { ErrorPageProps } from '@/types/page';
import { ErrorPage } from '@/ui/ErrorPage';

export const getStaticPaths: GetStaticPaths<{ subdomain: string }> = async () => {
  return {
    paths: [],
    fallback: 'blocking',
  };
};

export const getStaticProps: GetStaticProps<ErrorPageProps, { subdomain: string }> = async ({
  params,
}) => {
  if (!params) throw new Error('No path parameters found');

  const { subdomain } = params;
  const client = getLeavesClient(subdomain);

  try {
    const data = await client.getStaticProps('index');
    const { docsConfig } = data.deployment;

    return {
      props: {
        docsConfig,
      },
    };
  } catch (error) {
    if (isClientError(error) && error.response.status === 404) {
      return { props: {} };
    }

    console.error('Error fetching config for 404 page:', error);
    return { props: {} };
  }
};

export default ErrorPage;
